<template>
  <div class="my-address">
    <div class="topBgcolor"></div>

    <!-- 导航条 -->
    <van-nav-bar
      title="地址管理"
      left-text="返回"
      left-arrow
      :fixed='true'
      @click-left="back"
    />

    <!-- 头部背景图 -->
    <div class="my-bg">
      <div class="bg auto-img">
      </div>
    </div>

    <div class="list-box">
      <div v-if="list.length > 0">
        <van-address-list
          v-model="chosenAddressId"
          :list="list"
          default-tag-text="默认"
          @add="newAddress"
          @edit="editAddress"
        />
      </div>

      <!-- 无商品的列表背景 -->
      <div
        class="main-box-null"
        v-else
      >
        <van-empty
          class="custom-image"
          image="https://img.yzcdn.cn/vant/custom-empty-image.png"
          description="你还没填写收货地址，快去新增一个吧"
        >
          <van-button
            round
            color="#0b34ba"
            class="bottom-button"
            @click="newAddress"
          >
            马上去新增
          </van-button>
        </van-empty>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/address.css";
export default {
  data() {
    return {
      // 地址选择id
      chosenAddressId: "",
      // 地址列表数据
      list: [],
    };
  },

  created() {
    this.getReceiveAddress();
  },

  methods: {
    // 跳转上一步
    back() {
      this.$router.push({name:'My'});
    },

    newAddress() {
      this.$router.push({ name: "NewAddress" });
    },

    // 查询收货地址
    getReceiveAddress() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast('请先登录')
        return this.$router.push({name:'Login'});
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == 20000) {
            result.data.result.map((v) => {
              let address = v.province + v.city + v.county + v.addressDetail;
              this.list.push({
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address: address,
                isDefault: Boolean(v.isDefault),
              });
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 新增地址
    newAddress() {
      this.$router.push({ name: "NewAddress" });
    },

    // 编辑地址
    editAddress(item) {
      // 
      this.$router.push({ name: "NewAddress", query: { aid: item.id } });
    },
  },
};
</script>